.glozzom-navbar {
    .nav-link {
        font-size: 14px !important;
        text-transform: uppercase !important;
        padding-left: 1rem !important;
        padding-right: 1rem !important;
    }
    .active {
        border-left: #444 3px solid !important;
    }

}

#myCarousel {
    .carousel-item {
        height: 450px;
    }
    .carousel-image-1 {
        background: url("../../images/glozzom/image1.jpg");
        background-size: cover;
    }
    .carousel-image-2 {
        background: url("../../images/glozzom/image2.jpg");
        background-size: cover;
    }
    .carousel-image-3 {
        background: url("../../images/glozzom/image3.jpg");
        background-size: cover;
    }
}

#home-heading {
    position: relative;
    min-height: 200px;
    background: url("../../images/glozzom/lights.jpg") no-repeat fixed;
    text-align: center;
    color: white;
    .dark-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0 , 0, 0.7);
    }
}

#video-play {
    position: relative;
    min-height: 200px;
    background: url("../../images/glozzom/media.jpg") no-repeat fixed 0 -300px;
    text-align: center;
    color: white;
    .dark-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0 , 0, 0.7);
    }
    a {
        color: white;
    }
}

#page-header {
    height: 200px;
    background: url('../../images/glozzom/image1.jpg') fixed 0 -360px;
    color: #fff;
    border-bottom: 1px #eee solid;
    padding-top: 50px;
}

#about {
    .about-img {
        margin-top: -50px;
    }
}

#main-footer {
    background-color: #000;
    color: #ffffff;
}
